import React from 'react';

import sslLogo from '../../images/sslcommerz.png';

function SSLCommerzVerify() {
  return (
    <div className="verify">
      <div className="label">Verified by</div>
      <img src={sslLogo} alt="sslcommesrz" />
    </div>
  );
}

export default SSLCommerzVerify;
